import {Component} from '@angular/core';
import {Router} from '@angular/router-deprecated';
import {UtilService} from '../../shared/index';
import {User} from '../../shared/index';
import {AfterViewInit} from "@angular/core";
import {Response} from '@angular/http';
declare var jQuery:JQueryStatic;
/**
 * Created by Ping on 2016/5/10.
 */
@Component({
  selector: 'signup-box',
  providers: [UtilService],
  template: require('app/+signup/components/signup.component.html')
})

export class SignupComponent implements AfterViewInit{
  mobile:string; //
  isDisabled:boolean = false;
  isRegistered:boolean =false;
  user:User;
  message:string; //
  errorMsg:string;//
  error:boolean; //
  agree:boolean; //
  vdcode:string; //
  constructor(private router:Router,private _util:UtilService) {
    this.user = new User();
    this.message ='';
    this.error = false;
  }
  ngAfterViewInit() {
    jQuery('.ui.checkbox')
      .checkbox();
  }
  //
    toLogin() {
        this.router.parent.navigate(['Login']);
    }
  //
  countBack(){
    var i = 60;
    var intervalid = setInterval(()=>{
      i--;
      if(i>=1) {
        this.isDisabled = true; //
        this.message = i+'';
      } else {
        this.isDisabled = false; //
        this.message = '';
        clearInterval(intervalid);
      }
    }, 1000);
  }

  //
    getCode() {
      if(this.mobile){ //
        alert('');
        this._util.getValidCode(this.mobile).subscribe((res:Response)=>{
          var data = res.json();
          if(data.status ==="0"){
            this.errorMsg = '';
            this.error = true;
            setTimeout(() => {
              this.error = false;
            }, 10000);
            this.isRegistered = true;
          }else if(data.status ==="1"){
            this.vdcode = data.result;
          }
        });
        this.countBack();
      }else{
        alert("")
      }

    }
  //
  register(form:any) {
    if(this.isRegistered) {
      this.errorMsg = '';
      this.error = true;
      setTimeout(() => {
        this.error = false;
      }, 10000);
    } else if(form.validCode !== this.vdcode) {
      this.errorMsg = '';
      this.error = true;
      setTimeout(() => {
        this.error = false;
      }, 10000);
    } else if(form.password !== form.passwordConfirm) {
      this.errorMsg = '';
      this.error = true;
      setTimeout(() => {
        this.error = false;
      }, 10000);
    } else {
      this.user.phone = form.phone;
      this.user.password = form.password;
      this.user.email = form.email;
      this._util.signUp(JSON.stringify(this.user)).subscribe((res:Response)=>{
        var data = res.json();
        console.log(data); //
        if(data.status == "0"){
          this.errorMsg = '';
          this.error = true;
          setTimeout(() => {
            this.error = false;
          }, 10000);
        }else{
          alert(data.message);
          this.toLogin();
        }

      });
      //console.log(this.users);

    }

  }
}



